<template>
  <div style="text-align:center">
    <form class="form-style">
      <ul>
        <li>
          제목
        </li>
        <li>
          <input
            type="text"
            name="field2"
            class="field-style field-full align-right"
            placeholder="제목수정"
            v-model="Qna.title"
          />
        </li>
        <li>
          내용
        </li>
        <li>
          <textarea
            name="field5"
            class="field-style"
            placeholder="내용 입력"
            v-model="Qna.contents"
          ></textarea>
        </li>
        <li>
          <span @click.stop="updateQna">수정하기</span>
        </li>
      </ul>
    </form>
    <router-link to="/qna/list" class="btn btn-concrete"
      >목록으로 돌아가기</router-link
    >
  </div>
</template>

<script>
import Constant from "../../Constant";
export default {
  name: "updatepage",
  data() {
    return {
      Qna: {
        no: 0,
        id: "",
        title: "",
        regdate: "",
        contents: ""
      }
    };
  },
  created() {
    this.$store
      .dispatch(Constant.GET_Qna, { no: this.$route.params.no })
      .then(() => {
        this.Qna = { ...this.$store.state.Qna };
      });
  },
  methods: {
    updateQna() {
      console.log(this.Qna);
      this.$store.dispatch(Constant.MODIFY_Qna, { Qna: this.Qna });
      this.$router.push("/qna/list");
    }
  }
};
</script>

<style scoped>
.btn.btn-concrete {
  margin: 10px auto;
  background-color: #7e8c8d;
  border: 5px solid #7e8c8d;
  color: white;
  -webkit-box-shadow: 0 3px 0 #4e5b5c;
  box-shadow: 0 3px 0 #4e5b5c;
  text-decoration: none;
}
.btn.btn-concrete:hover {
  margin: 10px auto;
  border: 5px solid #6a7879;
  background-color: #6a7879;
  color: green;
  text-decoration: none;
}
.btn.btn-concrete:active {
  margin: 10px auto;
  top: 3px;
  outline: none;
  color: white;
  -webkit-box-shadow: none;
  box-shadow: none;
  text-decoration: none;
}

.form-style {
  max-width: 450px;
  background: #fafafa;
  padding: 30px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 15px;
  box-shadow: 1px 1px 25px rgba(0, 0, 0, 0.35);
  border-radius: 10px;
  border: 6px solid #08c27b;
}
.form-style ul {
  padding: 0;
  margin: 0;
  list-style: none;
}
.form-style ul li {
  display: block;
  margin-bottom: 10px;
  min-height: 35px;
}
.form-style ul li .field-style {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  padding: 8px;
  outline: none;
  border: 1px solid #3ab864;
}
.form-style ul li .field-style:focus {
  box-shadow: 0 0 5px #3dc58d;
  border: 1px solid #3cbd76;
}
.form-style ul li .field-split {
  width: 49%;
}
.form-style ul li .field-full {
  width: 100%;
}
.form-style ul li input.align-left {
  float: left;
}
.form-style ul li input.align-right {
  float: right;
}
.form-style ul li textarea {
  width: 100%;
  height: 100px;
}
.form-style ul li input[type="button"],
.form-style ul li input[type="submit"],
.form-style ul li span {
  -moz-box-shadow: inset 0px 1px 0px 0px #18be4f;
  -webkit-box-shadow: inset 0px 1px 0px 0px #00c94d;
  box-shadow: inset 0px 1px 0px 0px #00ce6e;
  background-color: #00a558;
  border: 1px solid #00993b;
  display: inline-block;
  cursor: pointer;
  color: #ffffff;
  padding: 8px 18px;
  text-decoration: none;
  font: 12px Arial, Helvetica, sans-serif;
}
.form-style ul li input[type="button"]:hover,
.form-style ul li input[type="submit"]:hover,
.form-style ul li span:hover {
  background: linear-gradient(to bottom, #7dff88 5%, #95eeb3 100%);
  background-color: #deffce;
}
</style>
