<template>
  <div id="app">
    <router-view></router-view>
  </div>
</template>

<script>
import NoticeList from "./notice/NoticeList.vue";
import NoticeInput from "./notice/NoticeInput.vue";
import NoticeUpdate from "./notice/NoticeUpdate.vue";
import NoticeDetail from "./notice/NoticeDetail.vue";
import VueRouter from "vue-router";
import Vue from "vue";
import Constant from "../Constant";
Vue.use(VueRouter);

const router = new VueRouter({
  //mode: "history",
  routes: [
    { path: "/", component: NoticeList },
    { path: "/notice/", component: NoticeList },
    { path: "/notice/list", component: NoticeList },
    { path: "/notice/input", component: NoticeInput },
    { path: "/notice/update/:no", component: NoticeUpdate },
    { path: "/notice/detail/:no", component: NoticeDetail }
  ]
});

export default {
  name: "noticeApp",
  components: {},
  router,
  data() {
    return {
      loginStatus: this.$store.state.loginStatus
    };
  },
  created() {
    this.$store
      .dispatch(Constant.GET_LOGINSTATUS)
      .then(() => (this.loginStatus = this.$store.state.loginStatus));
  }
};
</script>

