<template>
  <div style="text-align:center">
    <div class="title">
      <input type="text" class="tit_tit" v-model="Notice.title" readonly />
      <input type="text" class="tit_id" v-model="Notice.id" readonly />님
    </div>
    <div class="contents">
      <textarea v-model="Notice.contents" readonly></textarea>
    </div>
    <div>
      <div class="button_qna">
        <a @click.stop="updateNotice(Notice.no)"> 수정 </a>
        <a @click.stop="removeNotice(Notice.no)"> 삭제 </a>
      </div>
    </div>
    <router-link to="/notice/list" class="btn btn-concrete"
      >목록으로 돌아가기</router-link
    >
  </div>
</template>

<script>
import Constant from "../../Constant";
export default {
  name: "detailpage",
  computed: {
    Notice() {
      return this.$store.state.Notice;
    }
  },
  created() {
    this.$store
      .dispatch(Constant.GET_Notice, { no: this.$route.params.no })
      .then(() => {
        this.Notice = { ...this.$store.state.Notice };
      });
  },
  methods: {
    updateNotice(no) {
      this.$router.push("/notice/update/" + no);
    },
    removeNotice(no) {
      this.$store.dispatch(Constant.REMOVE_Notice, { no });
      this.$router.push("/notice/list");
    }
  }
};
</script>

<style scoped>
.form-style ul {
  padding: 0;
  margin: 0;
  list-style: none;
}
.tit_tit {
  background: none;
  border: none;
  margin: 0;
  font-size: 18px;
  width: 70%;
  font-weight: bold;
  margin-right: auto;
  margin-left: 1px;

  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}
.tit_id {
  background: none;
  border: none;
  text-align: right;
  width: 20%;
  margin: 0;
  font-size: 12px;
  font-weight: bold;
  margin-right: 1px;
  margin-left: auto;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  color: black;
}
.title {
  border-bottom: 3px solid #708827;
  width: 50%;
  margin: 20px;
  margin-left: auto;
  margin-right: auto;
  background: #ebf7c7;
  padding: 15px;
}
.contents {
  margin: 20px;
  margin-left: auto;
  margin-right: auto;
  border: none;
  background: none;
  width: 50%;
  height: 300px;
  padding: 15px;
  border-bottom: 3px solid #708827;
}
.contents textarea {
  width: 100%;
  border: none;
  height: 300px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  outline: none;
  overflow: auto;
  background: white;
}
.btn.btn-concrete {
  margin: 10px auto;
  background-color: #7e8c8d;
  border: 5px solid #7e8c8d;
  color: white;
  -webkit-box-shadow: 0 3px 0 #4e5b5c;
  box-shadow: 0 3px 0 #4e5b5c;
  text-decoration: none;
}
.btn.btn-concrete:hover {
  margin: 10px auto;
  border: 5px solid #6a7879;
  background-color: #6a7879;
  color: rgb(94, 204, 20);
  text-decoration: none;
}
.btn.btn-concrete:active {
  margin: 10px auto;
  top: 3px;
  outline: none;
  color: white;
  -webkit-box-shadow: none;
  box-shadow: none;
  text-decoration: none;
}
</style>
