<template>
  <div id="app">
    <router-view></router-view>
  </div>
</template>

<script>
import QnaList from "./qna/QnaList.vue";
import QnaInput from "./qna/QnaInput.vue";
import QnaUpdate from "./qna/QnaUpdate.vue";
import QnaDetail from "./qna/QnaDetail.vue";
import VueRouter from "vue-router";
import Vue from "vue";
import Constant from "../Constant";
Vue.use(VueRouter);

const router = new VueRouter({
  //mode: "history",
  routes: [
    { path: "/", component: QnaList },
    { path: "/qna/", component: QnaList },
    { path: "/qna/list", component: QnaList },
    { path: "/qna/input", component: QnaInput },
    { path: "/qna/update/:no", component: QnaUpdate },
    { path: "/qna/detail/:no", component: QnaDetail }
  ]
});

export default {
  name: "qnaApp",
  components: {},
  router,
  data() {
    return {
      loginStatus: {}
    };
  },
  created() {
    this.$store
      .dispatch(Constant.GET_LOGINSTATUS)
      .then(() => (this.loginStatus = this.$store.state.loginStatus));
    console.log(this.$store.state.loginStatus);
    console.log(this.loginStatus);
  }
};
</script>

