<template>
  <div style="text-align:center">
    <div style="text-align:right; margin-right:10%">
      <router-link
        to="/notice/input"
        class="btn btn-concrete"
        v-bind:class="{ writeBtn: !validUser }"
        >글쓰기</router-link
      >
    </div>
    <table class="qna">
      <thead>
        <tr style="text-align: center;">
          <th>번호</th>
          <th>제목</th>
          <th>작성자</th>
          <th>작성시간</th>
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="noticeItem in NoticeLists"
          v-bind:key="noticeItem.no"
          @click="getNotice(noticeItem.no)"
          style="text-align: center;"
        >
          <td>{{ noticeItem.no }}</td>
          <td>{{ noticeItem.title }}</td>
          <td>{{ noticeItem.id }}</td>
          <td>{{ noticeItem.regdate }}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import Constant from "../../Constant";

export default {
  name: "NoticeList",
  data() {
    return {
      loginStatus: this.$store.state.loginStatus,
      validUser: false
    };
  },
  computed: {
    NoticeLists() {
      return this.$store.state.NoticeLists;
    }
  },
  created() {
    this.$store.dispatch(Constant.GET_NoticeLIST).then(() => {
      this.NoticeLists = this.$store.state.NoticeLists;
    });
    this.$store.dispatch(Constant.GET_LOGINSTATUS).then(() => {
      this.loginStatus = this.$store.state.loginStatus;
      if (this.loginStatus.id == "admin" && this.loginStatus.status) {
        this.validUser = true;
      } else this.validUser = false;
    });
  },
  methods: {
    getNotice(no) {
      this.$router.push("/notice/detail/" + no);
    }
  }
};
</script>
<style scoped>
.writeBtn {
  display: none;
}
.btn.btn-concrete {
  margin: 10px auto;
  background-color: #7e8c8d;
  border: 5px solid #7e8c8d;
  color: white;
  -webkit-box-shadow: 0 3px 0 #4e5b5c;
  box-shadow: 0 3px 0 #4e5b5c;
  text-decoration: none;
}
.btn.btn-concrete:hover {
  margin: 10px auto;
  border: 5px solid #6a7879;
  background-color: #6a7879;
  color: orangered;
  text-decoration: none;
}
.btn.btn-concrete:active {
  margin: 10px auto;
  top: 3px;
  outline: none;
  color: white;
  -webkit-box-shadow: none;
  box-shadow: none;
  text-decoration: none;
}
table.qna {
  margin: 10%;
  margin-top: 15px;
  border-collapse: collapse;
  text-align: left;
  line-height: 1.2;
}
table.qna thead th {
  padding: 10px;
  font-weight: bold;
  vertical-align: center;
  color: white;
  border-bottom: 2px solid #676869;
  background: rgb(35, 189, 112);
}
table.qna tbody th {
  width: 150px;
  padding: 10px;
  font-weight: bold;
  vertical-align: center;
  border-bottom: 1px solid #ccc;
  background: #f3f6f7;
}
table.qna td {
  width: 350px;
  padding: 10px;
  vertical-align: center;
  border-bottom: 1px solid #ccc;
}
.button_qna {
  width: 50px;
  height: 35px;
  border: 2px solid #676869;
  display: inline-block;
  cursor: pointer;
  position: relative;
  box-sizing: border-box;
  overflow: hidden;
  margin: 0 auto;
  padding: 3px;
}
.button_qna a {
  color: #676869;
  text-decoration: none;
  transition: all 0.5s ease;
  z-index: 2;
  position: relative;
  margin: 0 auto;
}
.button_qna_eff {
  width: 50px;
  height: 20px;
  border: 0px solid #676869;
  position: absolute;
  transition: all 0.5s ease;
  z-index: 1;
  box-sizing: border-box;
}
.button_qna:hover {
  background: #676869;
}
.button_qna:hover a {
  color: #fff;
}
</style>
