<template>
  <div style="text-align:center">
    <form class="form-style" style="width:80%;margin:auto;">
      <ul>
        <li style="text-align:left;">
          <label class="switch">
            <input type="checkbox" v-bind:class="{'checked':isImpt(Notice.important)}">
            <span class="slider round"></span>
          </label>
          <label for="imporatant"></label>
          <strong>중요</strong>
          
          <input type="text" class="field-full" placeholder="제목입력" v-model="Notice.title"/>
          
          <input
            style="field-split"
            v-model="Notice.id"
            readonly
          />님
        </li>
        <li style="text-align:left;">
          <label for="category">카테고리</label>
          <select name="category" v-model="Notice.category">
            <option value="운영">운영</option>
            <option value="기타">기타</option>
          </select>
          내용
        </li>
        <li>
          <textarea
            name="field5"
            class="field-style"
            placeholder="내용 입력"
            v-model="Notice.contents"
          ></textarea>
        </li>
        <li>
          <span @click.stop="addNotice">등록하기</span>
        </li>
      </ul>
    </form>
    <router-link to="/notice/list" class="btn btn-concrete"
      >목록으로 돌아가기</router-link
    >
  </div>
</template>
<script>
import Constant from "../../Constant";
export default {
  data() {
    return {
      Notice: {
        no: 0,
        id: "",
        title: "",
        regdate: "",
        contents: ""
      },
      loginStatus: this.$store.state.loginStatus
    };
  },
  created() {
    this.$store.dispatch(Constant.GET_LOGINSTATUS).then(() => {
      this.loginStatus = this.$store.state.loginStatus;
      this.Notice.id = this.loginStatus.id;
    });
  },
  methods: {
    isImpt(important){
      return (important == true || important == 1)?true:false;
    },
    addNotice() {
      if (this.Notice.contents.trim() != "") {
        this.$store.dispatch(Constant.ADD_Notice, { Notice: this.Notice });
        this.$router.push("/notice");
        this.clear();
      }
    },
    clear() {
      (this.Notice.id = ""),
        (this.Notice.title = ""),
        (this.Notice.regdate = ""),
        (this.Notice.contents = "");
    }
  }
};
</script>

<style scoped>

/* The switch - the box around the slider */
.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: rgba(187,170,137);
}

input:focus + .slider {
  box-shadow: 0 0 1px rgba(187,170,137);
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}


.selectbox {
  position: relative;
  width: 100px; /* 너비설정 */
  border: 1px solid #999; /* 테두리 설정 */
  z-index: 1;
  /* focus 효과*/
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
  -webkit-transition: border-color ease-in-out 0.15s,
    -webkit-box-shadow ease-in-out 0.15s;
  -o-transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
  transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
}

/* focus 효과 */
.selectbox.focus {
  border-color: #66afe9;
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075),
    0 0 8px rgba(76, 61, 36, 0.8);
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075),
    0 0 8px rgba(76, 61, 36, 0.8);
}


.btn.btn-concrete {
  margin: 10px auto;
  background-color: #7e8c8d;
  border: 5px solid #7e8c8d;
  color: white;
  -webkit-box-shadow: 0 3px 0 #4e5b5c;
  box-shadow: 0 3px 0 #4e5b5c;
  text-decoration: none;
}
.btn.btn-concrete:hover {
  margin: 10px auto;
  border: 5px solid #6a7879;
  background-color: #6a7879;
  color: burlywood;
  text-decoration: none;
}
.btn.btn-concrete:active {
  margin: 10px auto;
  top: 3px;
  outline: none;
  color: white;
  -webkit-box-shadow: none;
  box-shadow: none;
  text-decoration: none;
}

.form-style {
  width : 100%;
  background: #fafafa;
  padding: 30px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 15px;
  box-shadow: 1px 1px 25px rgba(0, 0, 0, 0.35);
  border-radius: 10px;
  border: 6px solid rgba(128,81,56,0.35);
}
.form-style ul {
  padding: 0;
  margin: 0;
  list-style: none;
}
.form-style ul li {
  display: block;
  margin-bottom: 10px;
  min-height: 35px;
}
.form-style ul li .field-style {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  padding: 8px;
  outline: none;
  border: 1px solid rgba(187,170,137);
}
.form-style ul li .field-style:focus {
  box-shadow: 0 0 5px rgba(187,170,137,0.9);
  border: 1px solid rgba(187,170,137,0.6);
}
.form-style ul li .field-split {
   outline: none;
  width: 10%;
}
.form-style ul li .field-full {
  width: 50%;
  margin-left:auto;
  margin-right:auto;
}
.form-style ul li input.align-left {
  float: left;
}
.form-style ul li input.align-right {
  float: right;
}
.form-style ul li textarea {
  width: 100%;
  height: 100px;
}
.form-style ul li input[type="button"],
.form-style ul li input[type="submit"],
.form-style ul li span {
  -moz-box-shadow: inset 0px 1px 0px 0px rgba(193,190,182,0.6);
  -webkit-box-shadow: inset 0px 1px 0px 0px rgba(193,190,182,0.8);
  box-shadow: inset 0px 1px 0px 0px rgba(193,190,182);
  background-color: rgba(193,190,182);
  border: 1px solid rgba(179,159,130);
  display: inline-block;
  cursor: pointer;
  color: #ffffff;
  padding: 8px 18px;
  text-decoration: none;
  font: 12px Arial, Helvetica, sans-serif;
}
.form-style ul li input[type="button"]:hover,
.form-style ul li input[type="submit"]:hover,
.form-style ul li span:hover {
  background: linear-gradient(to bottom, rgba(179,159,130) 5%, rgba(193,190,182) 100%);
  background-color: rgba(223,210,192);
}
</style>

