<template>
  <div style="text-align:center">
    <div style="text-align:right; margin-right:10%">
      <router-link to="/qna/input" class="btn btn-concrete">글쓰기</router-link>
    </div>
    <table class="qna">
      <thead>
        <tr style="text-align: center;">
          <th>번호</th>
          <th>제목</th>
          <th>작성자</th>
          <th>작성시간</th>
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="qnaItem in QnaLists"
          v-bind:key="qnaItem.no"
          @click="getQna(qnaItem.no)"
          style="text-align: center;"
        >
          <td>{{ qnaItem.no }}</td>
          <td>{{ qnaItem.title }}</td>
          <td>{{ qnaItem.id }}</td>
          <td>{{ qnaItem.regdate }}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import Constant from "../../Constant";

export default {
  name: "QnaList",
  computed: {
    QnaLists() {
      return this.$store.state.QnaLists;
    }
  },
  created() {
    this.$store.dispatch(Constant.GET_QnaLIST);
  },
  methods: {
    getQna(no) {
      this.$router.push("/qna/detail/" + no);
    }
  }
};
</script>
