import Vue from "vue";
import Vuex from "vuex";
import Constant from "./Constant";
import http from "./http-common";
Vue.use(Vuex);

const store = new Vuex.Store({
  state: {
    QnaLists: [],
    Qna: {},
    NoticeLists: [],
    Notice: {},
    loginStatus: {}
  },
  actions: {
    // 로그인 정보 조회
    [Constant.GET_LOGINSTATUS]: store => {
      return new Promise((resolve, reject) => {
        http
          .get("/getLoginInfo.do")
          .then(response => {
            store.commit(Constant.GET_LOGINSTATUS, {
              loginStatus: response.data
            });
            resolve();
          })
          .catch(exp => {
            alert("로그인정보 조회 처리에 실패하였습니다." + exp);
            reject();
          });
      });
    },
    [Constant.GET_NoticeLIST]: store => {
      return new Promise((resolve, reject) => {
        http
          .get("api/notice")
          .then(response => {
            store.commit(Constant.GET_NoticeLIST, {
              NoticeLists: response.data.data
            });
            resolve();
          })
          .catch(exp => {
            alert("공지사항 조회에 실패하였습니다." + exp);
            reject();
          });
      });
    },
    [Constant.GET_Notice]: (store, payload) => {
      return new Promise((resolve, reject) => {
        http
          .get("api/notice/" + payload.no)
          .then(response => {
            store.commit(Constant.GET_Notice, { Notice: response.data.data });
            resolve();
          })
          .catch(() => {
            reject();
          });
      });
    },
    [Constant.REMOVE_Notice]: (store, payload) => {
      return new Promise((resolve, reject) => {
        http
          .delete("api/notice/" + payload.no)
          .then(() => {
            store.dispatch(Constant.GET_NoticeLIST);
            resolve();
          })
          .catch(exp => {
            alert("게시물 삭제 실패" + exp);
            reject();
          });
      });
    },
    [Constant.MODIFY_Notice]: (store, payload) => {
      return new Promise((resolve, reject) => {
        http
          .put("/api/notice", {
            //일부만 가능한지 모르겠다만
            important: payload.Notice.important,
            category: payload.Notice.category,
            title: payload.Notice.title,
            contents: payload.Notice.contents,
            regdate: payload.Notice.regdate,
            no: payload.Notice.no
          })
          .then(() => {
            store.dispatch(Constant.GET_NoticeLIST);
            resolve();
          })
          .catch(exp => {
            alert("수정 처리에 실패하였습니다." + exp);
            reject();
          });
      });
    },
    [Constant.ADD_Notice]: (store, payload) => {
      return new Promise((resolve, reject) => {
        let isimportant = true;
        if (payload.Notice.important == "중요") {
          isimportant = true;
        } else isimportant = false;
        http
          .post("api/notice", {
            category: payload.Notice.category,
            contents: payload.Notice.contents,
            id: payload.Notice.id,
            important: isimportant,
            title: payload.Notice.title
          })
          .then(() => {
            store.dispatch(Constant.GET_NoticeLIST);
            resolve();
          })
          .catch(exp => {
            alert("추가 처리에 실패하였습니다." + exp);
            reject();
          });
      });
    },
    [Constant.GET_QnaLIST]: store => {
      return new Promise((resolve, reject) => {
        http
          .get("api/qna")
          .then(response => {
            store.commit(Constant.GET_QnaLIST, {
              QnaLists: response.data.data
            });
            resolve();
          })
          .catch(exp => {
            alert("게시물 조회에 실패하였습니다." + exp);
            reject();
          });
      });
    },
    [Constant.GET_Qna]: (store, payload) => {
      return new Promise((resolve, reject) => {
        http
          .get("api/qna/" + payload.no)
          .then(response => {
            console.log("조회하였습니다.");
            store.commit(Constant.GET_Qna, { Qna: response.data.data });
            resolve();
          })
          .catch(() => {
            console.log("조회에 실패하였습니다.");
            reject();
          });
      });
    },
    [Constant.REMOVE_Qna]: (store, payload) => {
      return new Promise((resolve, reject) => {
        http
          .delete("api/qna/" + payload.no)
          .then(() => {
            store.dispatch(Constant.GET_QnaLIST);
            resolve();
          })
          .catch(exp => {
            alert("게시물 삭제에 실패하였습니다." + exp);
            reject();
          });
      });
    },
    [Constant.MODIFY_Qna]: (store, payload) => {
      return new Promise((resolve, reject) => {
        http
          .put("api/qna", {
            id: payload.Qna.id,
            no: payload.Qna.no,
            title: payload.Qna.title,
            contents: payload.Qna.contents,
            regdate: payload.Qna.regdate
          })
          .then(() => {
            //업데이트 하고 나서 다시 전체 리스트 뿌려주는 걸로
            store.dispatch(Constant.GET_QnaLIST);
            resolve();
          })
          .catch(exp => {
            alert("수정 처리에 실패하였습니다." + exp);
            reject();
          });
      });
    },
    [Constant.ADD_Qna]: (store, payload) => {
      return new Promise((resolve, reject) => {
        http
          .post("api/qna", {
            id: payload.Qna.id,
            title: payload.Qna.title,
            regdate: payload.Qna.regdate,
            contents: payload.Qna.contents
          })
          .then(() => {
            store.dispatch(Constant.GET_QnaLIST);
            resolve();
          })
          .catch(() => {
            console.log("추가에 실패하였습니다.");
            reject();
          });
      });
    }
  },

  mutations: {
    // 저장소에 데이터 실제 반영(commit시 호출됨.)
    [Constant.GET_QnaLIST]: (state, payload) => {
      state.QnaLists = payload.QnaLists;
    },
    [Constant.GET_Qna]: (state, payload) => {
      state.Qna = payload.Qna;
    },
    [Constant.GET_NoticeLIST]: (state, payload) => {
      state.NoticeLists = payload.NoticeLists;
    },
    [Constant.GET_Notice]: (state, payload) => {
      state.Notice = payload.Notice;
    },
    [Constant.GET_LOGINSTATUS]: (state, payload) => {
      state.loginStatus = payload.loginStatus;
    }
  }
});

export default store;
