<template>
  <div id="app">
    <router-view></router-view>
  </div>
</template>

<script>
import NoticeApp from "./components/NoticeApp";
import QnaApp from "./components/QnaApp";
import VueRouter from "vue-router";
import Vue from "vue";
Vue.use(VueRouter);

const router = new VueRouter({
  mode: "history",
  routes: [
    { path: "/notice.jsp", component: NoticeApp },
    { path: "/qna.jsp", component: QnaApp }
  ]
});

export default {
  name: "app",
  components: {},
  router
};
</script>

